.post{
    width: 35%;
    border: 1px solid lightgray;
    background-color: white;
    box-sizing: border-box;
    margin-top: 10px;
    margin-bottom: 10px;
}

.post-header{
    padding: 10px 20px ;
    display: flex;
    justify-content: space-between;
    align-items: center;
    box-sizing: border-box;
}

.post-header-left{
    display: flex;
    align-items: center;
}

.post-header-left>h5{
    margin-left: 10px;
}

.post-header-right>button{
    background-color: transparent;
    border: 0;
    outline: none;
    color: #009FF8;
    font-weight: 550;
    cursor: pointer;
}

.post-photo-style{
    width: 100%;
    object-fit: contain;
}

.post-footer{
    padding: 10px 20px;
    box-sizing: border-box;
}

.post-footer>h4{
    font-weight: normal;
}

.post-footer>h4>strong{
    margin-right: 5px;
}

.post-footer>div{
    display: flex;
    align-items: center;
}

.post-footer>div>span{
    margin-right: 5px;
}

.post-comment{
    display: flex;
    justify-content: center;
    align-items: center;
}

.post-comment>input{
    flex: 1;
    margin-bottom: 0;
}

.post-comment>button{
    height: 30px;
    border-radius: 0%;
    border: 0;
    outline: none;
    margin-bottom: 0px;
    margin-top: 5px;
}

.show-comment{
    padding:0px 20px;
}

.show-comment>h5{
    font-weight: normal;
}

.show-comment>h5>strong{
    margin-right: 5px;
}



.comment-btn{
    border: 0;
    outline: none;
    background-color: transparent;
    cursor: pointer;
    color:#009FF8;
    font-weight: 550;

}

@media screen and (min-width:360px) and (max-width:640px) {
    .post{
        width: 80%;
    }
 }
 
 @media screen and (min-width:768px) and (max-width:1024px)  {
       .post{
           width: 60%;
       }
 }