.signin-container{
    display: flex;
    justify-content:center;
    align-items: center;
    width: 100%;
    height: 100vh;
    background-color: #fafafa;
}

.signin{
    width: 25%;
    text-align: center;
}

.signin-top{
    background-color: white;
    border: 1px solid lightgray;
}


.signin-logo{
    padding: 20px ;
}

.instagram-login-logo{
    object-fit: contain;
    width: 45%;
}

.signin-form{
    display: flex;
    flex-direction: column;
    padding: 20px 50px;
}

.input-field{
    height: 30px;
    margin: 5px 0px;
    font-size: 13px;
    padding: 5px;
    box-sizing: border-box;
    background-color: #fafafa;
    border: 1px solid lightgray;

}

.btn{
    height: 30px;
    margin: 10px 0px;
    border: 0;
    outline: none;
    background-color: #0095F6;
    color: white;
    font-size: 15px;
    font-weight: bold;
    cursor: pointer;
    border-radius: 5px;
}

.signin-bottom{
    border: 1px solid lightgray;
    background-color: white;
    margin: 20px 0px;
}

.signin-bottom>h5{
    font-weight: normal;
    font-size: 17px;
}

.navlink-style{
    text-decoration: none;
    color: #009FF8;
    font-weight: bold;
}


@media screen and (min-width:360px) and (max-width:640px) {
    .signin{
        width: 80%;
    }

    .signin-logo{
        padding: 10px;
    }

    .signin-form{
        padding: 15px;
    }
}

@media screen and (min-width:768px) and (max-width:1024px)  {
    .signin{
        width: 50%;
    }   
}