}

.signin-form>h5{
    font-weight: normal;
    margin: 0px;
    color:#8E8EAB ;
}

.signin-form>h5>span{
    font-weight: bold;
}
.home{
    background-color: #fafafa;
}

.home-header{
    position: -webkit-sticky;
    position: sticky;
    top:0;
    z-index: 10; 
    padding: 20px;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
