.post{
    width: 35%;
    border: 1px solid lightgray;
    background-color: white;
    box-sizing: border-box;
    margin-top: 10px;
    margin-bottom: 10px;
}

.post-header{
    padding: 10px 20px ;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center;
    box-sizing: border-box;
}

.post-header-left{
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
}

.post-header-left>h5{
    margin-left: 10px;
}

.post-header-right>button{
    background-color: transparent;
    border: 0;
    outline: none;
    color: #009FF8;
    font-weight: 550;
    cursor: pointer;
}

.post-photo-style{
    width: 100%;
    object-fit: contain;
}

.post-footer{
    padding: 10px 20px;
    box-sizing: border-box;
}

.post-footer>h4{
    font-weight: normal;
}

.post-footer>h4>strong{
    margin-right: 5px;
}

.post-footer>div{
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
}

.post-footer>div>span{
    margin-right: 5px;
}

.post-comment{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
}

.post-comment>input{
    -webkit-flex: 1 1;
            flex: 1 1;
    margin-bottom: 0;
}

.post-comment>button{
    height: 30px;
    border-radius: 0%;
    border: 0;
    outline: none;
    margin-bottom: 0px;
    margin-top: 5px;
}

.show-comment{
    padding:0px 20px;
}

.show-comment>h5{
    font-weight: normal;
}

.show-comment>h5>strong{
    margin-right: 5px;
}



.comment-btn{
    border: 0;
    outline: none;
    background-color: transparent;
    cursor: pointer;
    color:#009FF8;
    font-weight: 550;

}

